export default {

	data() {
		return {
			btconnected: false,
			battery: 50
		}
	},
	onLoad() {
		uni.showLoading({
			title: '连接中',
			mask: true
		})
		//连接蓝牙
		this.connectBle()
	},
	methods: {
		connectBle() {
			var that = this
			setTimeout(function() {
				that.btconnected = true
				uni.hideLoading()
			}, 4000)
		}
	}
}
